import { Component, OnInit } from "@angular/core";
import { SellerMatchListingService } from "./sellerMatchListing.service";
import { StateServiceService } from "../../../../../state-service.service";
import { ShortNamePipe } from '../../../../../short-name.pipe'
@Component({
  selector: "app-sellerMatchListing",
  templateUrl: "./sellerMatchListing.component.html",
  styleUrls: ["./sellerMatchListing.component.css"]
})
export class SellerMatchListingComponent implements OnInit {
  return: any;
  userData: any;
  isLoggedIn: boolean;
  isLoading: boolean = false;
  user: any;
  uid: any;
  propertyDetails = [];
  matchedProperties = [];
  unmatchedProperties = [];
  distanceInKm;
  listingSeller: any;
  noOfMatches: any;
  noOfUnmatched: any;
  isThanku: boolean = true;
  userName: any;
  name: any;
  match: any;
  users: any;
  Names: any;
  unNamed: any;
  title: any;
  hello: boolean = false;
  sort: any;
  drop: any;
  country: any;
  low: any;
  high: any;
  unMatched: any;
  distinctExpressedUid = [];
  less: any;
  more: any;
  expressed: any;
  removesDataSpace: any;
  removesSellerSpace: any;
  minAmount: any;
  maxAmount: any;
  removePostcodeSpace: any;
  removePostcodeStateSpace: any;
  stateMaxAmount: any;
  constructor(
    private SellermatchesService: SellerMatchListingService,
    private stateService: StateServiceService
  ) { }

  ngOnInit() {
    this.user = JSON.parse(localStorage.getItem("user"));
    this.uid = this.user.uid;
    // Fetch details
    this.SellermatchesService.getSellerProperties(this.uid).subscribe(res => {
      res.forEach(item => {

        //Min-MAx Amount Removed Comas and Formula
        this.minAmount = item.data().MinAmount.replace(/,/g, "");
        this.maxAmount = item.data().MaxAmount.replace(/,/g, "");
        this.stateMaxAmount = this.stateService.listingSeller.MaxAmount.replace(/,/g, "");
        this.less = (this.minAmount - (this.minAmount * 3) / 100);
        this.more = (this.maxAmount * 1 + (this.maxAmount * 10) / 100 * 1);

        //Select First Three Number of Postcode
        this.removePostcodeSpace = item.data().Lookingpostcode.replace(/\s/g, "")
        this.removePostcodeStateSpace = this.stateService.listingSeller.Lookingpostcode.replace(/\s/g, "")



        //Result Set 1  Matches
        if (
          item.data().Lookingpostcode == this.stateService.listingSeller.Lookingpostcode &&
          item.data().PropertyType == this.stateService.listingSeller.PropertyType &&
          this.more >= this.stateService.listingSeller.MaxAmount.replace(/,/g, "") &&
          this.less <= this.stateService.listingSeller.MaxAmount.replace(/,/g, "")
        ) {

          this.matchedProperties.push({
            detail: item.data(),
            propertyId: item.id,
          }); //Use this object to populate html
        }

        //Result set 2 Matches
        else if (this.removePostcodeSpace != this.removePostcodeStateSpace &&
          item.data().PropertyType == this.stateService.listingSeller.PropertyType &&
          this.more >= this.stateService.listingSeller.MaxAmount.replace(/,/g, "") &&
          this.less <= this.stateService.listingSeller.MaxAmount.replace(/,/g, "") &&
          this.removePostcodeSpace.substring(0, 3) == this.removePostcodeStateSpace.substring(0, 3)) {
          this.matchedProperties.push({
            detail: item.data(),
            propertyId: item.id,
          }); //Use this object to populate html
        }

        // Result Set 1 No Matches
        else if (
          item.data().PropertyType ==
          this.stateService.listingSeller.PropertyType &&
          this.more >= this.stateService.listingSeller.MaxAmount.replace(/,/g, "") &&
          this.less <= this.stateService.listingSeller.MaxAmount.replace(/,/g, "") &&
          item.data().latitude &&
          item.data().longitude
        ) {
          this.distanceInKm = this.getDistanceFromLatLonInKm(
            this.stateService.listingSeller.latitude,
            this.stateService.listingSeller.longitude,
            item.data().latitude,
            item.data().longitude
          );
          this.unmatchedProperties.push({
            detail: item.data(),
            propertyId: item.id,
            distance: this.distanceInKm,

          });
        }

        // Result Set 2 No Matches
        else if (
          this.removePostcodeSpace != this.removePostcodeStateSpace &&
          item.data().PropertyType !=
          this.stateService.listingSeller.PropertyType &&
          item.data().LookingTown == this.stateService.listingSeller.LookingTown &&
          item.data().latitude &&
          item.data().longitude
        ) {
          this.distanceInKm = this.getDistanceFromLatLonInKm(
            this.stateService.listingSeller.latitude,
            this.stateService.listingSeller.longitude,
            item.data().latitude,
            item.data().longitude
          );
          this.unmatchedProperties.push({
            detail: item.data(),
            propertyId: item.id,
            distance: this.distanceInKm,

          });
        }

      });

      this.unmatchedProperties.sort(this.compare);
      this.noOfMatches = this.matchedProperties.length;
      this.noOfUnmatched = this.unmatchedProperties.length;
      //console.log(this.matchedProperties[0]);

    });
    this.getExpressedListingIds();
  }

  getDistanceFromLatLonInKm(lat1, lon1, lat2, lon2) {
    var R = 6371; // Radius of the earth in km
    var dLat = this.deg2rad(lat2 - lat1); // deg2rad below
    var dLon = this.deg2rad(lon2 - lon1);
    var a =
      Math.sin(dLat / 2) * Math.sin(dLat / 2) +
      Math.cos(this.deg2rad(lat1)) *
      Math.cos(this.deg2rad(lat2)) *
      Math.sin(dLon / 2) *
      Math.sin(dLon / 2);
    var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    var d = R * c; // Distance in km
    return d;
  }

  deg2rad(deg) {
    return deg * (Math.PI / 180);
  }
  compare(a, b) {
    const distA = a.distance;
    const distB = b.distance;

    let comparison = 0;
    if (distA > distB) {
      comparison = 1;
    } else if (distA < distB) {
      comparison = -1;
    }
    return comparison;
  }
  closeThank() {
    this.isThanku = false
  }
  getShortName(fullName) {
    return fullName.split(' ').map(n => n[0]).join('');
  }
  test(value) {
    if (value == this.sort.low) {
      this.hello = true
    }

  }

  hightoLow() {
    this.matchedProperties.sort(function (a, b) {
      return b.MaxAmount - a.MaxAmount
    })
  }
  lowtohigh() {
    this.matchedProperties.sort(function (a, b) {
      return a.MaxAmount - b.MaxAmount
    })
  }
  roomshightolow() {
    this.matchedProperties.sort(function (a, b) {
      return a.Maxrooms - b.Maxrooms
    })
  }
  matchedsearch() {
    this.matchedProperties.sort(function (a, b) {
      return a.SearchRadius - b.SearchRadius
    })
  }


  //For unmatched
  unmatchedhightoLow() {
    this.unmatchedProperties.sort(function (a, b) {
      return b.detail.MaxAmount - a.detail.MaxAmount
    })
  }
  unmatchedlowtohigh() {
    this.unmatchedProperties.sort(function (a, b) {
      return a.detail.MaxAmount - b.detail.MaxAmount
    })
  }
  unmatchedroomshightolow() {
    this.unmatchedProperties.sort(function (a, b) {
      return a.detail.Maxrooms - b.detail.Maxrooms
    })
  }
  unmatchedmatchedsearch() {
    this.unmatchedProperties.sort(function (a, b) {
      return a.detail.SearchRadius - b.detail.SearchRadius
    })
  }
  unmatcheddistance() {
    this.unmatchedProperties.sort(function (a, b) {
      return a.distance - b.distance
    })
  }

  getExpressedListingIds() {
    this.SellermatchesService.ExpressInterest(this.uid).subscribe((ref) => {
      ref.forEach((item) => {
        if (this.distinctExpressedUid.indexOf(item.data().propertyId) === -1) {
          this.distinctExpressedUid.push(item.data().propertyId);
        }
      });

      console.log(this.distinctExpressedUid);
    });
  }

  checkExpressForValue(uid) {
    if (this.distinctExpressedUid.includes(uid)) {
      this.expressed = true
      return true;
    } else {
      this.expressed = false
      false;
    }
  }


}
